<template>
    <v-container fluid id="section-portofolio">
        <v-row class="mb-10 d-flex flex-column"
            style="min-height: 100vh;background-color: white;">
            <v-col class="d-flex flex-column" cols="12">
                <v-container>
                    <P class="text-uppercase font-weight-black text-h4">my portofolio</P>
                    <v-divider class=""></v-divider>
                </v-container>
            </v-col>
            <v-col class="d-flex flex-column" cols="12">
                <v-container>
                    <v-tabs color="#333333">
                        <v-tab @click="changeDisplay('all')">All</v-tab>
                        <v-tab @click="changeDisplay('design')">Design</v-tab>
                        <v-tab @click="changeDisplay('app')">Application</v-tab>

                        <v-tab-item v-for="n in 3" :key="n">
                            <v-container fluid>
                                <v-row>
                                    <v-col
                                        v-for="(item, i) in dataDisplay"
                                        :key="i"
                                        cols="12"
                                        md="4"
                                        class="portofolio-card"
                                        @click="ChangeDialogImage(item.id, item.title)">
                                        <v-img
                                            :src="require('../assets/portofolio/'+item.src)"
                                            :lazy-src="require('../assets/portofolio/white.jpg')"
                                            contain
                                            ></v-img>
                                        <!-- <p>
                                            {{ item.title }}
                                        </p> -->
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-tab-item>
                    </v-tabs>
                </v-container>
            </v-col>
        </v-row>
        <v-dialog
            v-model="dialog"
            scrollable
            width="">
            <v-card>
                <v-card-title class="headline grey lighten-2">
                    {{ detailImageTitle }}
                </v-card-title>

                <v-card-text>
                    <v-carousel
                        hide-delimiters
                        height="">
                        <v-carousel-item
                            v-for="(item, i) in detailImage.detail" 
                            :key="i">
                            <div class="" align="center">
                            <v-img
                                :src="require('../assets/portofolio/'+item.src)"
                                :lazy-src="require('../assets/portofolio/white.jpg')"
                                :width="item.height !== '' ? item.height : ''"
                                contain
                                ></v-img>
                            </div>
                        </v-carousel-item>
                    </v-carousel>
                </v-card-text>

                <v-divider></v-divider>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-container>
</template>

<style scoped>
    .portofolio-card {
        transition: .5s
    }
    .portofolio-card:hover {
        cursor: pointer;
        background-color: #f2f2f2;
    }
</style>

<script>
    export default {
        name: 'PortofolioSection',

        created() {
            this.changeDisplay('all')
        },
        components: {
        },

        data: () => ({
            dialog: false,
            items: [
                'all', 'design', 'application'
            ],
            dataDisplay: [],
            detailImageTitle: '',
            detailImage: [],
            data: [
                {
                    id: 1,
                    title: 'flip the card',
                    src: 'flip-the-card.png',
                    tag: 'app',
                    detail: [
                        {
                            title: '',
                            src: 'flip-the-card.png',
                            height: '',
                        },
                        {
                            title: '',
                            src: 'flip-the-card-1.png',
                            height: '',
                        },
                        {
                            title: '',
                            src: 'flip-the-card-2.png',
                            height: '',
                        },
                        {
                            title: '',
                            src: 'flip-the-card-3.png',
                            height: '280',
                        },
                        {
                            title: '',
                            src: 'flip-the-card-4.png',
                            height: '280',
                        },
                        {
                            title: '',
                            src: 'flip-the-card-5.png',
                            height: '280',
                        },
                    ]
                },
                {
                    id: 2,
                    title: 'football team',
                    src: 'football.jpg',
                    tag: 'design',
                    detail: [
                        {
                            title: '',
                            src: 'football.jpg',
                            height: '',
                        },
                        {
                            title: '',
                            src: 'football-1.jpg',
                            height: '',
                        },
                        {
                            title: '',
                            src: 'football-2.jpg',
                            height: '',
                        },
                    ]
                },
                {
                    id: 3,
                    title: 'vue web',
                    src: 'vue-project.png',
                    tag: 'design',
                    detail: [
                        {
                            title: '',
                            src: 'vue-project-1.png',
                            height: '',
                        },
                    ]
                },
                {
                    id: 4,
                    title: 'arknight web',
                    src: 'arknight.png',
                    tag: 'design',
                    detail: [
                        {
                            title: '',
                            src: 'arknight-1.png',
                            height: '',
                        },
                    ]
                },
                {
                    id: 5,
                    title: 'brawstar web',
                    src: 'brawstar.jpg',
                    tag: 'design',
                    detail: [
                        {
                            title: '',
                            src: 'brawstar-1.jpg',
                            height: '',
                        },
                    ]
                },
            ],
        }),

        methods: {
            changeDisplay(tags){
                this.dataDisplay = []
                if (tags == 'all') {
                    this.dataDisplay = this.data
                }
                else {
                    for (let i = 0; i < this.data.length; i++) {
                        if (this.data[i].tag == tags) {
                            this.dataDisplay.push(this.data[i]);
                        }
                    }
                }
            },
            ChangeDialogImage(id, title){
                this.detailImageTitle = title
                this.detailImage = this.data.find(q => q.id == id);
                this.dialog = true
            }
        }
    }
</script>